@charset "UTF-8";
/*  I. Font settings
 * --------------------------------------- */ 
@font-face {
  font-family: "GillSansC";
  src: local("GillSansC Regular"), local("GillSansC-Regular"), url("../fonts/GillSansC-Regular.woff2") format("woff2"), url("../fonts/GillSansC-Regular.woff") format("woff"), url("../fonts/GillSansC-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "GillSansC";
  src: local("GillSansC Light"), local("GillSansC-Light"), url("../fonts/GillSansC-Light.woff2") format("woff2"), url("../fonts/GillSansC-Light.woff") format("woff"), url("../fonts/GillSansC-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "GillSansC";
  src: local("GillSansC Bold"), local("GillSansC-Bold"), url("../fonts/GillSansC-Bold.woff2") format("woff2"), url("../fonts/GillSansC-Bold.woff") format("woff"), url("../fonts/GillSansC-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "GillSansC";
  src: local("GillSansC BoldItalic"), local("GillSansC-BoldItalic"), url("../fonts/GillSansC-BoldItalic.woff2") format("woff2"), url("../fonts/GillSansC-BoldItalic.woff") format("woff"), url("../fonts/GillSansC-BoldItalic.ttf") format("truetype");
  font-weight: 700;
  font-style: italic;
  font-display: swap;
}
/* II. Styles reset
 * --------------------------------------- */
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  border: 0;
  outline: 0;
}

[hidden] {
  display: none;
}

button,
input {
  border: none;
  outline: none;
  border-radius: 0;
}

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em;
}

i {
  display: inline-block;
}

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}

blockquote,
q {
  quotes: blockquote, q rgba(136, 34, 34, 0) blockquote, q rgba(136, 34, 34, 0);
}
blockquote:before, blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

ul, ol {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*  III. Default styles
 * --------------------------------------- */
html {
  font-size: 10px;
}

h1, h2, h3, h4, h5, h6,
p {
  cursor: default;
}

.container, .container_xxl, .container_xl, .container_lg, .container_md, .container_sm {
  margin: 0 auto;
  padding: 0 1.2rem;
  max-width: 135rem;
}

.d-block {
  display: block;
}
.d-inline {
  display: inline;
}
.d-none {
  display: none;
}

.row {
  margin: 0 -1.2rem;
  display: flex;
  flex-wrap: wrap;
}

.scroll-hidden {
  padding-right: 17px;
  overflow: hidden;
}

.wrapper {
  margin: 0 auto;
  max-width: 1920px;
  overflow: hidden;
  position: relative;
}

address {
  font-style: normal;
}

body {
  font-family: "GillSansC";
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.15;
  color: #000000;
}

h1 {
  font-size: 6.2rem;
  font-weight: 400;
  line-height: 0.84;
  letter-spacing: -0.01em;
}

.text-light {
  color: #fff;
}
.text-uppercase {
  text-transform: uppercase;
}
.text-accent {
  color: #e6c282;
}
.text-bold {
  font-weight: 700;
}

a, a:visited {
  text-decoration: none;
  outline: none;
  color: #f00;
  transition: color 0.3s ease-in-out;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button,
input[type=submit],
input[type=reset],
input[type=button],
.btn {
  font-family: "GillSansC";
  text-decoration: none;
  cursor: pointer;
  user-select: none;
}

.btn {
  padding: 2rem 2rem;
  display: inline-block;
  text-align: center;
  border-radius: 1rem;
  font-size: 1.6rem;
  text-transform: uppercase;
  background: none;
}
.btn-wrap_primary {
  position: relative;
}
.btn-wrap_primary::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -4px;
  z-index: 0;
  display: block;
  width: 100%;
  height: 70%;
  border-radius: 2rem;
  background: linear-gradient(87.93deg, #755A2B 18.42%, #DCB673 84.37%);
  box-shadow: -1px 13px 23px rgba(0, 28, 125, 0.59);
}
.btn-wrap_primary:hover::after {
  background: linear-gradient(87.93deg, #755A2B 18.42%, #DCB673 84.37%);
  box-shadow: -1px 13px 23px rgba(0, 28, 125, 0.59);
}
.btn-wrap_primary:active::after {
  background: none;
  box-shadow: none;
}
.btn_primary {
  position: relative;
  z-index: 5;
  color: #846123;
  background: linear-gradient(122.01deg, rgba(225, 147, 75, 0.6) 4.06%, rgba(209, 172, 128, 0) 26.3%), linear-gradient(200.47deg, rgba(255, 253, 233, 0.55) 3.11%, rgba(255, 252, 225, 0) 39.68%), linear-gradient(88.22deg, #846123 2.03%, #E6C282 38.74%);
}
.btn_primary:hover {
  color: #573B09;
  background: linear-gradient(122.01deg, rgba(225, 147, 75, 0.6) 4.06%, rgba(209, 172, 128, 0) 26.3%), linear-gradient(219.68deg, rgba(255, 253, 233, 0.97) 4.99%, rgba(255, 252, 225, 0) 42.7%), linear-gradient(87.14deg, #846123 2.31%, #F0CA85 26.52%);
}
.btn_primary:active {
  color: #573B09;
  background: linear-gradient(90deg, #846123 -5.65%, #F0CA85 100%);
  box-shadow: 0px 0px 24px rgba(66, 187, 255, 0.35), inset 0px -2px 1px rgba(255, 255, 255, 0.19), inset 0px 4px 15px rgba(0, 0, 0, 0.41);
}
.btn_fluid {
  width: 100%;
}

input:not(input[type=checkbox]),
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
}

input::-ms-input-placeholder {
  color: #C6C6C6;
}

input::-webkit-input-placeholder {
  color: #C6C6C6;
}

input::-moz-placeholder {
  color: #C6C6C6;
}

input::-ms-clear {
  display: none;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

select::-ms-expand {
  display: none;
}

