/* Modal 
 * -------------------------*/
.modal-trigger {
  cursor: pointer;
}

.modal {
  padding: 2rem;
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  overflow: auto;
  opacity: 0;
  visibility: hidden;
}
.modal__content {
  margin-top: 0rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  max-width: 114rem;
  height: auto;
  max-height: calc(100vh - 20rem);
  position: relative;
  z-index: 1001;
  border-radius: 1.2rem;
  font-size: 2.4rem;
  line-height: 1.15;
  opacity: 0;
}
.modal__content h3 {
  font-size: 2.8rem;
  font-weight: 400;
  margin-top: 1em;
  margin-bottom: 0.5em;
}
.modal__content ol, .modal__content ul {
  margin-top: 0.5em;
  padding-left: 0.75em;
}
.modal__content ol li, .modal__content ul li {
  margin-bottom: 0.5em;
}
.modal__content p {
  margin-bottom: 1em;
}
.modal__content-inner {
  display: flex;
  flex-direction: column;
  padding: 3rem 0rem 3.5rem 0rem;
  width: 100%;
  border-radius: 1.2rem;
  background-color: #ffffff;
  overflow: auto;
}
.modal__header {
  padding-right: 2.5rem;
  padding-right: 3rem;
  padding-left: 3rem;
  position: relative;
  border-bottom: solid 1px #C6C6C6;
}
.modal__header .modal__title {
  margin-bottom: 1.2rem;
  text-transform: uppercase;
  font-size: 3.6rem;
  font-weight: 400;
  text-align: center;
}
.modal__header p {
  font-size: 2rem;
  font-weght: 400;
}
.modal__body {
  padding-top: 1.2rem;
  padding-right: 3rem;
  padding-left: 3rem;
  flex-grow: 1;
  text-align: justify;
  overflow: auto;
}
.modal__footer {
  padding-top: 0rem;
}
.modal__close {
  padding: 0;
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  top: -6%;
  right: -8%;
  z-index: 999;
  border: none;
  background: none;
}
.modal-close__item {
  display: block;
  width: 2.3rem;
  height: 2px;
  position: absolute;
  left: 0;
  top: 50%;
  border-radius: 2px;
  background-color: #fff;
  transition: background-color 0.3s;
}
.modal-close__item:first-child {
  transform: rotate(45deg);
}
.modal-close__item:last-child {
  transform: rotate(-45deg);
}
.modal-close:hover .modal-close__item {
  background-color: rgba(255, 255, 255, 0.5);
}
.modal__overlay {
  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
}
.modal.opened {
  animation: fadeIn 0.5s ease-out forwards;
}
.modal.opened .modal__content {
  animation: slideUpIn 0.3s ease-out forwards;
}
.modal.opened .modal__overlay {
  animation: fadeIn 0.5s ease forwards;
}
.modal.closed {
  animation: fadeOut 0.5s ease-in forwards;
}
.modal.closed .modal__content {
  animation: slideUpOut 0.3s ease-in forwards;
}
.modal.closed .modal__overlay {
  animation: fadeOut 0.5s ease-in forwards;
}

/* Lazy 
 * -------------------------*/
/* ANIMATIONS
 * ------------------------------  */
.fake-class-to-add-import-rule {
  visibility: inherit;
}

.d-anim-delay-1000 {
  animation-delay: 1000ms;
}
.d-anim-delay-1100 {
  animation-delay: 1100ms;
}
.d-anim-delay-1200 {
  animation-delay: 1200ms;
}

.slideUpIn {
  animation-name: slideUpIn;
}

/* VI. Animations
 * --------------------------------------- */
@keyframes fadeIn {
  from {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    visibility: visible;
    opacity: 1;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
@keyframes slideUpIn {
  from {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(10%);
    transform: translateY(10%);
  }
  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
  }
}
@keyframes slideUpOut {
  from {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
  }
  to {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(-10%);
    transform: translateY(-10%);
  }
}
/* VII. Media-queries
 * --------------------------------------- */
@media (max-width: 1699.98px) {
  html {
    font-size: 9px;
  }
  .header-heading {
    max-width: 82rem;
  }
  .hero__container {
    max-width: 1400px;
  }
  .hero__row {
    margin: 0 0;
